background: url('https://steamuserimages-a.akamaihd.net/ugc/924805627747624904/2DBF60E8C96C13F0E52A8916469428CA3896847B/')no-repeat;
width: 475px;
height: 360px;
padding: 125px 0;
background-position: center;
