.page {
  position: fixed;
}


#p2, #p3, #p4, #p5 {
  left: 200%;
}

#p1 { background: rgb(26, 23, 36); }
#p2 { background: rgba(255, 99, 71, 0.699); }
#p3 { background: rgba(129, 24, 77, 0.719); }
#p4 { background: rgba(145, 61, 241, 0.432); }
#p5 { background: rgba(123, 139, 230, 0.459); }

#t2:target #p2,
#t3:target #p3,
#t4:target #p4,
#t5:target #p5 {
  transform: translateX(-190%);
  -webkit-transform: translateX(-190%);
  -moz-transform: translateX(-190%);
  -o-transform: translateX(-190%);
  transition-delay: .4s !important;
}

#t2:target #p1{
  background: rgb(207, 205, 216)
}

#t3:target #p1{
  background: rgb(28, 27, 34)
}

#t4:target #p1{
  background: rgb(13, 13, 15)

}

#t5:target #p1{
  background: rgb(177, 79, 76);
}

.icon {
  height: 100px;
  width: 100px;
  display: block;
  margin: auto;
  border: 3px;
  padding: 1px;
  padding-left: 10px;
  padding-right: 10px;
  
}

ul .icon:hover {
  opacity: 0.5;
}

.page .icon .title {
  line-height: 2;
}

